import { CSVLink } from "react-csv";

const Table = props => {
    const handleSubmit = (e) => {
        const inputs = document.querySelectorAll(".driverList");
        const stints = [...props.stints];
        inputs.forEach((e, index) => {
            stints[index].driver = e.value;
        });
        props.setStints(stints);
    };

    return (
        <div className="tableComponent">
            <div className="table">
                <table>
                    <thead>
                        <tr>
                            <th colSpan={11}>Stints</th>
                        </tr>
                        <tr>
                            <th rowSpan={2}>Stint #</th>
                            <th colSpan={3}>Game time</th>
                            <th colSpan={3}>Real life time</th>
                            <th rowSpan={2}>Amount of laps</th>
                            <th rowSpan={2}>Time left at the end</th>
                            <th rowSpan={2}>Fuel required</th>
                            <th rowSpan={2}>Driver</th>
                        </tr>
                        <tr>
                            <th>Start time</th>
                            <th>In</th>
                            <th>Out</th>
                            <th>Start time</th>
                            <th>In</th>
                            <th>Out</th>
                        </tr>
                    </thead>
                    <tbody>
                        {props.stints.map((x) => {
                            return <tr key={Math.random()}>
                                <td>{x.stintNumber}</td>
                                <td>{x.gameStartTime}</td>
                                <td>{x.gameComingIn}</td>
                                <td>{x.gameComingOut}</td>
                                <td>{x.rlStartTime}</td>
                                <td>{x.rlComingIn}</td>
                                <td>{x.rlComingOut}</td>
                                <td>{x.amountOfLapsInStint}</td>
                                <td>{`${Math.floor(x.timeLeftOver/60)}min ${x.timeLeftOver%60}s`}</td>
                                <td>{`${x.fuelRequired}L`}</td>
                                <td><input 
                                    type="input"
                                    className="driverList"
                                ></input></td>
                            </tr>
                        })}
                    </tbody>
                </table>
            </div>
            <div className="csvButton">
                <CSVLink
                    data={props.stints}
                    filename={"stints.csv"}
                    className="btn"
                    target="_blank"
                    onClick={handleSubmit}
                >
                    Export to CSV
                </CSVLink>
            </div>
        </div>
    )
}

export default Table;